.banner__top,
.banner__midle,
.banner__bottom {
  height: 33%;
  position: relative;
}

.banner__text {
  font-size: 35px;
  width: 284px;
  font-weight: bold;
  left: 50%;
  margin-left: -142px;
  color: white;
  -webkit-text-stroke: 1px black;
  position: absolute;
  margin-top: 0;
  margin-bottom: 0;
}

.banner__cube-img {
  position: absolute;
  z-index: 999;
  width: 124px;
  left: 50%;
  margin-left: -62px;
  top: 40%;
  margin-top: -62px;
  -webkit-animation: cubeAnimation 4s infinite steps(10, end);
          animation: cubeAnimation 4s infinite steps(10, end);
}

@-webkit-keyframes cubeAnimation {
  50% {
    top: 60%;
  }
}

@keyframes cubeAnimation {
  50% {
    top: 60%;
  }
}

.banner__midle-text {
  z-index: 0;
  font-size: 100px;
  top: 50%;
  margin-top: -60px;
  white-space: nowrap;
  -webkit-text-stroke: 0px;
  color: pink;
  -webkit-animation: endlessRotation 7s infinite linear;
          animation: endlessRotation 7s infinite linear;
}

/* 
Describes seamless endless rotation rtl
I'm using scale to make text taller.
*/
@-webkit-keyframes endlessRotation {
  0% {
    -webkit-transform: scale(1, 2.5) translate(0);
            transform: scale(1, 2.5) translate(0);
  }
  to {
    -webkit-transform: scale(1, 2.5) translate(-585%);
            transform: scale(1, 2.5) translate(-585%);
  }
}
@keyframes endlessRotation {
  0% {
    -webkit-transform: scale(1, 2.5) translate(0);
            transform: scale(1, 2.5) translate(0);
  }
  to {
    -webkit-transform: scale(1, 2.5) translate(-585%);
            transform: scale(1, 2.5) translate(-585%);
  }
}

